.about-me {
  max-width: 90vw;
  margin: 8rem auto auto -9rem;
  padding: 1rem;
  background: rgba(229, 240, 198, 0.822);
  border-left: 5px solid black;
  font-family: 'Courier New', monospace;
  font-size: 1.25rem;
  line-height: 1.6;
  color: #404d31;
  text-align: left;
}

/* Mobile override */
@media (max-width: 768px) {
  .about-me {
    margin-top: 0rem;
    font-size: 1rem;
  }
}