.typewriter {
  font-family: 'Courier New', monospace;
  font-size: 6rem;
  position: absolute; 
  top: 2rem;
  left: 2rem;
  display: inline-block;
  color: black;
}

.cursor {
  display: inline-block;
  width: 1ch;
  animation: blink 1s step-start infinite;
  color: black;
}

.cursor.on {
  opacity: 1;
}

@keyframes blink {
  50% {
    opacity: 0;
  }
}

/* Mobile override */
@media (max-width: 768px) {
  .typewriter {
    font-size: 2.5rem;
    margin-left: 1rem;
    margin-top: 1rem;
  }
}
