.project{
  max-width: 90vw;
  margin: 8rem auto auto -9rem;
  padding: 2rem;
  background: rgba(229, 240, 198, 0.822);
  border-left: 5px solid black;
  font-family: 'Courier New', monospace;
  font-size: 1.25rem;
  line-height: 1.6;
  color: #404d31;
  text-align: left;
}

a{
    color:#404d31;
    text-decoration: none;
    font-weight: lighter;
    font-style: italic;
}

a:hover{
    text-decoration: underline;
    color: darkgreen;
}


ul{
    list-style-type: disc;
    margin-left: -1rem;
    font-family: 'Courier New', monospace;
    font-size: 1.1rem;
    line-height: 1.6;
}

/* Mobile override */
@media (max-width: 768px) {
  .project {
    margin-top: 0.5rem;
    font-size: 1rem;
  }
}