.contact{
  max-width: 90vw;
  margin: 8rem auto auto -9rem;
  padding: 2rem;
  background: rgba(229, 240, 198, 0.822);
  border-left: 5px solid black;
  font-family: 'Courier New', monospace;
  font-size: 1.25rem;
  line-height: 1.6;
  color: #404d31;
  text-align: left;
}

.contact-icons{
    display: flex;
    gap: 1.5rem;
    margin-top: 1rem;
}

.icon{
    font-size: 2.5rem;
}

.icon:hover{
    cursor: pointer;
}

/* Mobile override */
@media (max-width: 768px) {
  .contact {
    margin-top: 0.5rem;
    font-size: 1rem;
  }
}